<template>
  <div class="commentpge">
    <div class="box">
      <div class="outgo-box" @click="goPrenext">
        <van-icon name="arrow-left" size="25" />
      </div>
      <div class="mi-cont">
        <span>{{ leng }}条评论</span>
      </div>
    </div>
    <h5>{{ leng }}条品论</h5>
    <div class="com-cont" v-for="(item, index) in comArr" :key="index">
      <div class="com-img">
        <img :src="item.avatar" alt="" />
      </div>
      <div class="com-cont-box">
        <div class="com-name">
          <div class="cont-text">
            <span>{{ item.author }}</span>
          </div>
          <div class="del">
            <span>...</span>
          </div>
        </div>
        <div class="com-cont-conent">
          <p>
            {{ item.content }}
          </p>
          <span class="replycom-bar" v-if="item.reply_to">{{item.reply_to.author}} 回复 {{item.author}}: {{item.reply_to.content}}</span>
          <span class="replycom">{{ item.ropalytext }}</span>
        </div>
        <div class="com-date">
          <div class="now">
            <span>{{ item.time }}</span>
          </div>
          <div class="nice">
            <div class="left">
              <span>{{ item.likes }}</span>
              <van-icon
                name="good-job"
                :color="item.flag ? '#ec393c' : '#ccc'"
                size="15"
                @click="likepre(index)"
              />
            </div>
            <div class="right" @click="replycom(item.author, index)">
              <img src="../assets/huif.png" alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="fot">
      <span>已显示全部评论</span>
    </div>
    <div class="send-out" @click="prego">
      <div class="send-img">
        <img :src="imgStr" alt="" />
      </div>
      <div class="send-text">
        <span>说说你的看法...</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "commentpge",
  data() {
    return {
      comId: "",
      comArr: [],
      flag: false,
      leng: 0,
      imgStr: require("../assets/rto.png"),
    };
  },
  created() {
     let nan = JSON.parse(localStorage.getItem('preser'));
     if(!nan){
        this.$toast('请先填写个人资料')
        setTimeout(()=>{
          this.$router.push({name:'midata'})
        },1000)
        return;
      }
    this.comId = this.$route.params.id;
    // console.log(this.comId);
    let len = JSON.parse(localStorage.getItem("len")) || [];

    let str = JSON.parse(localStorage.getItem("str"));
    //  console.log(str);
    if (str.length >= 1) {
      // console.log(str[str.length-1]);
      this.imgStr = str[str.length - 1].url;
    }
    this.axios({
      method: "get",
      url: `https://apis.netstart.cn/zhihudaily/story/${this.comId}/short-comments`,
    })
      .then((resdata) => {
        let rescomArr = resdata.data.comments;
        // console.log(rescomArr);
        for (let i = 0; i < rescomArr.length; i++) {
          rescomArr[i].time = this.gettimer(rescomArr[i].time);
          rescomArr[i].oid = this.comId;
          rescomArr[i].flag = false;
        }
        // len.push()
        this.comArr = rescomArr;
        this.leng = this.comArr.length;
        // console.log(this.comArr);
        localStorage.setItem("len", JSON.stringify(rescomArr));
      })
      .catch((err) => {
         for (let i = 0; i < len.length; i++) {
          len[i].time = this.gettimer(len[i].time);
          len[i].oid = this.comId;
          len[i].flag = false;
        }
        this.comArr = len;
        this.leng = this.comArr.length;
        // console.log(err);
      });
  },
  methods: {
    // 返回上一级
    goPrenext() {
      // console.log(this.comId);
      let id = localStorage.getItem("id") ;
      if (!this.comId) {
        this.$router.push({ name: "contpage", params: { id } });
      } else {
        this.$router.push({ name: "contpage", params: { id: this.comId } });
      }
    },
    // 处理时间的函数
    gettimer(timer) {
      let t = timer * 1000;
      let date = new Date(t);
      let getmound = date.getMonth() + 1;
      let getday = date.getDate();
      let gethous = date.getHours();
      let getsecd = date.getMinutes();

      getmound = getmound > 10 ? getmound : "0" + getmound;
      getday = getday > 10 ? getday : "0" + getday;
      gethous = gethous > 10 ? gethous : "0" + gethous;

      let str = "";
      let nowdater = new Date().getDate();
      nowdater = nowdater > 10 ? nowdater : "0" + nowdater;
      if (getday === nowdater) {
        str = `今天${gethous}:${getsecd}`;
      } else {
        str = `${getmound}-${getday}${'\xa0'}${gethous}:${getsecd}`;
      }

      return str;
    },

    replycom(aut, index) {
      // console.log(aut, index);
      this.$router.push({ name: "writecom", params: { m: aut, index:index,my:true } });
    },
    // 回复个人评论
    prego() {
      this.$router.push({ name: "writecom" ,params:{my:false}});
    },
    // 个人评论点赞
    likepre(index) {
      this.flag = !this.flag;
      let len = JSON.parse(localStorage.getItem("len")) || [];
        if (this.flag) {
          this.comArr[index].likes = this.comArr[index].likes + 1;
          this.comArr[index].flag = this.flag;
          len[index].likes = len[index].likes + 1;
          len[index].flag = this.flag;
        } else {
          this.comArr[index].likes = this.comArr[index].likes - 1;
          this.comArr[index].flag = this.flag;
          len[index].likes = len[index].likes - 1;
          len[index].flag = this.flag;
        }
      localStorage.setItem("len", JSON.stringify(len));
    },
  },
  mounted() {
    // console.log("下面==>", this.comArr);
  },
};
</script>

<style lang="less" scoped>
.commentpge {
  position: relative;
  padding-bottom: 50px;
}
.replycom-bar{
  color: #ccc;
}
.replycom{
  color: #ddd;
}
.box {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid rgba(143, 140, 140, 0.2);
  display: flex;
  align-items: center;
  .mi-cont {
    margin-left: 125px;
    font-size: 18px;
    font-weight: bold;
  }
}
h5 {
  padding: 10px 0 0 10px;
  margin: 0;
}

.com-cont {
  width: 100%;
  display: flex;
  padding-top: 10px;
  border-bottom: 1px solid rgba(143, 140, 140, 0.2);
}
.com-img {
  width: 40px;
  text-align: right;
  img {
    display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
  }
}
.com-cont-box {
  width: calc(100% - 70px);
  padding: 0 10px;
  .com-name {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 3px;
    .cont-text {
      font-size: 14px;
      font-weight: bold;
    }
  }
  .com-cont-conent {
    width: 100%;
    margin-bottom: 3px;
    p {
      width: 100%;
      margin: 0;
      padding: 0;
      word-wrap: break-word;
    }
  }
  .com-date {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
    .now {
      color: #ccc;
    }

    .nice {
      width: 60px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .right {
        width: 15px;
        img {
          width: 100%;
          height: 15px;
        }
      }
    }
  }
}

.fot {
  width: 100%;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #ccc;
}
.send-out {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  color: rgb(172, 167, 167);
  background-color: #ddd;
  position: fixed;
  bottom: 0;
  .send-img {
    width: 30pxpx;
    height: 30px;
    margin-right: 5px;
    img {
      display: inline-block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
}
</style>